@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Oswald.woff2") format("woff2"), url("../../fonts/Oswald.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Arial.woff2") format("woff2"), url("../../fonts/Arial.woff") format("woff");
  font-display: swap;
}
.wrapper {
  margin: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 30px;
  }
  .wrapper--form {
    padding: 0 39px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    margin: 0 110px;
  }
  .wrapper--form {
    padding: 0;
  }
  .wrapper--form-personality {
    margin-right: 0;
  }
  .wrapper--form-radio {
    margin-left: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.link {
  text-decoration: none;
  cursor: pointer;
}

.button {
  display: block;
  font-family: "Oswald", "Calibri", "Helvetica", sans-serif;
  color: #ffffff;
  background-color: #69b838;
  border: none;
  text-transform: uppercase;
  padding: 7px 44px;
  cursor: pointer;
  box-sizing: border-box;
}
.button--loading {
  background-color: #f2f2f2;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.title {
  font-family: "Oswald", "Calibri", "Helvetica", sans-serif;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.image {
  display: block;
  max-width: 100%;
}

.page {
  height: 100%;
  font-family: "Arial", "Calibri", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 1440px) {
  .page {
    max-width: 1440px;
    margin: 0 auto;
  }
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
  margin: 0;
}

.page-body__page-header {
  position: relative;
  min-height: 64px;
}
@media (min-width: 768px) {
  .page-body__page-header {
    margin: 25px 0 64px;
  }
}
@media (min-width: 1440px) {
  .page-body__page-header {
    margin: 53px 0 111px;
  }
}

.page-body__page-header--catalog::after,
.page-body__page-header--form::after {
  content: "";
  position: absolute;
  bottom: -17.5px;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
@media (min-width: 768px) {
  .page-body__page-header--catalog::after,
.page-body__page-header--form::after {
    content: none;
  }
}
@media (min-width: 1440px) {
  .page-body__page-header--catalog,
.page-body__page-header--form {
    margin: 54px 0 74px;
  }
}

.page-header--index {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .page-header {
    margin-right: 30px;
  }
  .page-header--index {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .page-header {
    margin-right: 110px;
  }
}

.page-header__logo {
  padding: 10px 0 17px;
  cursor: pointer;
}

.page-header__logo-image {
  display: block;
  width: 191px;
  height: 37px;
}
@media (min-width: 768px) {
  .page-header__logo-image {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo-image {
    width: 202px;
    height: 59px;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-nav {
  position: relative;
}
@media (min-width: 768px) {
  .page-nav {
    margin-right: 0;
  }
}

.page-nav__toggle {
  position: absolute;
  top: -40px;
  right: 20px;
  z-index: 2;
  width: 24px;
  height: 16px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-nav__toggle {
    display: none;
  }
}

.page-nav--closed .page-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}

.page-nav--opened .page-nav__toggle::before, .page-nav--opened .page-nav__toggle::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  z-index: 1;
}
.page-nav--opened .page-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-nav--opened .page-nav__toggle::after {
  transform: rotate(-45deg);
}

.page-nav__list {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
}
@media (min-width: 768px) {
  .page-nav__list {
    position: relative;
    background-color: transparent;
  }
}

.page-nav--nojs .page-nav__toggle {
  display: none;
}

@media (min-width: 320px) {
  .page-nav--closed .nav-list__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-nav--closed .nav-list__item {
    display: block;
  }
}

.page-nav--nojs .nav-list__item {
  display: block;
}

@media (min-width: 768px) {
  .nav-list {
    display: flex;
    column-gap: 36px;
  }
}

.nav-list__item {
  position: relative;
  display: block;
  font-size: 20px;
  line-height: 20px;
  padding: 22px;
  text-align: center;
}
.nav-list__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
}
.nav-list__item:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .nav-list__item {
    padding: 0;
  }
  .nav-list__item::before, .nav-list__item:last-child::after {
    content: none;
  }
}

.nav-list__link {
  font-family: "Oswald", "Calibri", "Helvetica", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  color: #000000;
}
@media (min-width: 768px) {
  .nav-list__link {
    font-size: 18px;
    line-height: 24px;
  }
  .nav-list__link--current::after, .nav-list__link--current-index::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #69b838;
  }
}
@media (min-width: 1440px) {
  .nav-list__link {
    font-size: 20px;
    line-height: 30px;
  }
  .nav-list__link--index {
    color: #ffffff;
  }
  .nav-list__link--current-index::after {
    background-color: #ffffff;
  }
}

.page-main {
  flex-grow: 1;
}

.page-main__promo {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .page-main__promo {
    margin-bottom: 0;
  }
}

.page-main__programs {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .page-main__programs {
    margin-bottom: 45px;
  }
}
@media (min-width: 1440px) {
  .page-main__programs {
    margin-bottom: 71px;
  }
}

.page-main__process {
  position: relative;
  margin-bottom: 49px;
}
@media (min-width: 768px) {
  .page-main__process {
    margin-bottom: 90px;
  }
  .page-main__process::after {
    content: "";
    position: absolute;
    bottom: -43px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #cdcdcd;
  }
}
@media (min-width: 1440px) {
  .page-main__process {
    margin-bottom: 69px;
  }
  .page-main__process::after {
    content: none;
  }
}

.page-main__example {
  margin-bottom: 26px;
}
@media (min-width: 768px) {
  .page-main__example {
    margin-bottom: 60px;
  }
}
@media (min-width: 1440px) {
  .page-main__example {
    margin-bottom: 0;
  }
}

.promo {
  position: relative;
  text-align: center;
}
.promo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 302px;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/index/promo-background-image.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -2;
}
@media (min-resolution: 2dppx) {
  .promo::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/index/promo-background-image@2x.jpg");
  }
}
@media (min-width: 768px) {
  .promo::after {
    content: none;
  }
}
@media (min-width: 1440px) {
  .promo::after {
    content: "";
    width: 720px;
    height: 694px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/index/promo-background-image-desktop.jpg");
    top: -207px;
    right: 0;
    left: auto;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .promo::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/index/promo-background-image-desktop@2x.jpg");
  }
}
.promo::before {
  content: "";
  position: absolute;
  top: 90px;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 280px;
  height: 270px;
  background-image: url("../../img/index/promo-image.png");
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}
@media (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../../img/index/promo-image@2x.png");
  }
}
@media (min-width: 768px) {
  .promo::before {
    content: "";
    width: 709px;
    height: 609px;
    background-image: url("../../img/index/promo-image-tablet.png");
    top: 230px;
    left: -64px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../../img/index/promo-image-tablet@2x.png");
  }
}
@media (min-width: 1440px) {
  .promo::before {
    content: "";
    width: 552px;
    height: 499px;
    background-image: url("../../img/index/promo-image-desktop.png");
    top: -230px;
    right: 330px;
    left: auto;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../../img/index/promo-image-desktop@2x.png");
  }
}
@media (min-width: 768px) {
  .promo {
    padding-bottom: 526px;
  }
}
@media (min-width: 1440px) {
